header {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  width: 100%;
  height: 10vh;
  min-height: 5rem;
  margin: 0 auto;
  padding: 0 $gutter;
  vertical-align: top;
  display: flex;
  justify-content: space-between;
  z-index: 1;

  a:hover {
    @extend %cw_text_link_outline;
  }
  h1 {
    @include breakpoint(small_down) {
      position: absolute;
      top: $gutter;
      left: $gutter;
    }
    a {
      @extend %cw_main_font_color;
    }
  }

}

header nav {
  margin-top: .74em;
  margin-right: -.7em;

  @include breakpoint(small_down) {
    position: absolute;
    left: 0;
    top: 3.3em;
    margin: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  @include breakpoint (medium_down) {
    a:first-child {
      display: none;
    }
  }

  a {
    @extend %nav-item;
    @include breakpoint(small_down) {
      padding: .7em 2%;
    }
  }
}



